
import { defineComponent, ref } from 'vue'
import { getTeamTrainInfo } from '@/service/team-train-info'

export default defineComponent({
  name: 'TeamTrainInfo',
  setup() {
    const detailInfo = ref([{}])
    getTeamTrainInfo().then((res) => {
      detailInfo.value = res
    })
    return {
      detailInfo
    }
  }
})
