import { dfRequest } from '../request'

export interface UserItem {
  username: string
  name: string
  department: string | null
  major: string | null
  joinTime: string | null
  allTrainningTime: string | null
  admin: boolean
  email: string | null
}

export function getTeamTrainInfo(): Promise<UserItem[]> {
  return new Promise((resolve) => {
    dfRequest
      .post({
        url: '/api/getall/'
      })
      .then((res) => {
        // console.log('全队的训练记录', res)
        resolve(res.data.users)
      })
      .catch((err) => {
        console.log('获取队伍训练数据失败', err)
      })
  })
}
